.Hygge {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.Logo {
  width: 40vh;
  height: 40vh;
  position: fixed;
  bottom: -10%;
  right: -10%;
  z-index: -1;
}

@media (min-width: 500px) {
  .Logo {
    right: 10%;
  }
}
